import { FullWinner } from '../types';
import info from './info';
import paginationControl from './paginationControl';
import winnersItem from './winnersItem';

export const renderWinners = async (winnersDb: FullWinner[], page: number) => {
  const garageBody = document.querySelector('.winners__table-body');
  const cars = winnersDb
    .map((e, i) => winnersItem((i + 1 + (page - 1) * 10), e.wins, e.name, e.time, e.color, e.car));
  if (garageBody) {
    garageBody.innerHTML = cars.join('');
  }
};

export default () => `
<div class="winners">
  ${info('winners')}
  <table class="winners__table">
    <thead class="winners__table-head">
      <th data-sort="id" >Number</th>
      <th>Car</th>
      <th>Name</th>
      <th data-sort="wins" >Wins</th>
      <th data-sort="time" >Best time</th>
    </thead>
    <tbody class="winners__table-body">
    </tbody>
  </table>
  ${paginationControl('winners')}
</div>
`;
