import './assets/styles/stile.scss';
import createBody from './ts/view/body';
import garage from './ts/view/garage';
import winners from './ts/view/winners';
import { getCars } from './ts/services/carService';
import {
  renderGarageInformation, garageControlListener,
} from './ts/controller/garage';
import { garageWinnersListener, winnersControl } from './ts/controller/winners';

(async () => {
  await createBody();
  renderGarageInformation();
  garageControlListener();
  garageWinnersListener();
})();

const main = document.querySelector('.main');

getCars([{ key: 'limit', value: 7 }, { key: 'page', value: 1 }]);

const routes = {
  '/': { title: 'Garage', render: garage, info: renderGarageInformation },
  '/winners': { title: 'Garage', render: winners, info: winnersControl },
};

function router() {
  const view = routes[window.location.pathname as keyof typeof routes];
  if (view && main) {
    document.title = view.title;
    main.innerHTML = view.render();
    view.info();
  } else {
    window.history.replaceState('', '', '/');
    router();
  }
}

window.addEventListener('click', (e: MouseEvent) => {
  const target = e.target as HTMLTextAreaElement;
  if (target.matches('[data-link]')) {
    e.preventDefault();
    window.history.pushState('', '', target.getAttribute('href'));
    router();
  }
});

window.addEventListener('popstate', router);
window.addEventListener('DOMContentLoaded', router);
