import { Path, QueryParams, WinnersDB } from '../types';

const baseUrl = 'http://localhost:3000';

const getQueryParams = (params: QueryParams): string => {
  if (params.length) {
    return `?${params.map((p) => `_${p.key}=${p.value}`).join('&')}`;
  }
  return '';
};

export const getWinners = async (queryParams: QueryParams) => {
  const response = await fetch(`${baseUrl}${Path.winners}${getQueryParams(queryParams)}`);
  const winners: WinnersDB = await response.json();
  const totalCount = response.headers.get('X-Total-Count');
  return { winners, totalCount };
};

export const getWinner = async (id: number) => {
  const response = await fetch(`${baseUrl}${Path.winners}/${id}`);
  const car = await response.json();
  if (response.status === 404) {
    return false;
  }
  return car;
};

export const createWinner = async (body: {
  id: number,
  wins: number,
  time: number
}) => {
  const response = await fetch(`${baseUrl}${Path.winners}`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(body),
  });
  const winner = await response.json();
  return winner;
};

export const deleteWinner = async (id: number) => {
  const response = await fetch(`${baseUrl}${Path.winners}/${id}`, {
    method: 'DELETE',
  });
  const car = await response.json();
  return car;
};

export const updateWinner = async (id: number, body: { wins: number, time: number }) => {
  const response = await fetch(`${baseUrl}${Path.winners}/${id}`, {
    method: 'PUT',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(body),
  });
  const car = await response.json();
  return car;
};
