import { CarType } from '../types';
import finish from '../../assets/icons/finish.svg';
import smog from '../../assets/images/smog.png';

export const makeStopCar = (carBox: Element) => {
  const car = carBox.querySelector('.car__car') as HTMLElement;
  const carA = carBox?.querySelector('.car__a') as HTMLButtonElement;
  const carB = carBox?.querySelector('.car__b') as HTMLButtonElement;
  const smogView = carBox?.querySelector('.car__smog') as HTMLButtonElement;
  smogView.classList.remove('_active');
  carB.disabled = true;
  carA.disabled = false;
  car.style.transform = 'translate(0px)';
};

export const makeMoveCar = (carBox: Element, time: number, winnerMake) => {
  const car = carBox.querySelector('.car__car') as HTMLElement;
  const { clientWidth } = (carBox.querySelector('.car__road') as HTMLElement);
  const btnA = carBox.querySelector('.car__a') as HTMLButtonElement;
  const btnB = carBox.querySelector('.car__b') as HTMLButtonElement;
  btnA.disabled = true;
  btnB.disabled = false;

  const animation = car?.animate([
    {
      transform: `translate(${clientWidth - 100}px)`,
    },
  ], time);
  animation.addEventListener('finish', (el: AnimationPlaybackEvent) => {
    winnerMake(el);
    car.style.transform = `translate(${clientWidth - 100}px)`;
  });

  return animation;
};

export default ({
  color, name, id, car = 4,
}: CarType) => `
  <div id=${id} class="car ">
    <div class="car__header">
      <button data-select class="car__select">select</button>
      <button data-remove class="car__remove">remove</button>
      <div class="car__name">${name}</div>
    </div>
    <div class="car__track">
      <div class="car__control">
        <button data-start class="car__a">a</button>
        <button disabled data-stop class="car__b">b</button>
      </div>
      <div class="car__road">
        <div id=${name} style="color: ${color};" class="car__car _icon-cart-${car}">
        <img
        class="car__smog"
          src=${smog}
          alt="Checkered Racing Flag Variant Vector - Finish Flag Sprite @clipartmax.com"
        />
        </div>
        <img
        class="car__finish"
          src=${finish}
          alt="Checkered Racing Flag Variant Vector - Finish Flag Sprite @clipartmax.com"
        />
      </div>
    </div>
  </div>
`;
