import controls from './controls';
import info from './info';
import car from './car';
import { CarDB } from '../types';
import paginationControl from './paginationControl';

export const renderCart = async (carsDb: CarDB) => {
  const garageBody = document.querySelector('.garage__body');
  const cars = carsDb.map((e) => car({
    color: e.color, name: e.name, id: e.id, car: e.car,
  }));
  if (garageBody) {
    garageBody.innerHTML = cars.join('');
  }
};

export default () => `
<div class="garage">
  ${controls()}
  ${info('garage')}
  <div class="garage__body">
  </div>
    ${paginationControl('garage')}
</div>
`;
