export const renderNotifyMessage = async (text: string, type: 'success' | 'info' | 'error') => {
  const notify = document.createElement('div');
  notify.classList.add('notify');
  notify.classList.add(`notify-${type}`);
  notify.innerHTML = `
  <i class="notify__icon"></i>
  <div class="notify__text">
    <span notify__title></span>
    <span>${text}</span>
  </div>
`;
  document.body.append(notify);
  setTimeout(() => {
    notify.remove();
  }, 5000);
};

export default () => `
`;
