import { Path } from '../types';

const baseUrl = 'http://localhost:3000';

export const startStopEngine = async (id: number, status: 'started' | 'stopped') => {
  const response = await fetch(`${baseUrl}${Path.engine}?id=${id}&status=${status}`, {
    method: 'PATCH',
  });
  const params = await response.json();
  return params;
};

export const startCar = async (id: number, status: 'drive') => {
  const response = await fetch(`${baseUrl}${Path.engine}?id=${id}&status=${status}`, {
    method: 'PATCH',
  });
  if (response.status === 500) {
    return false;
  }
  const params = await response.json();
  return params;
};
