export const getRandom = (min: number, max: number) => Math
  .floor(Math.random() * (max - min + 1) + min);
const models = ['Model S', 'Model 3', 'Model X', 'Model Y', 'Semi', 'Cybertruck', 'Roadster', 'A-Class', 'C-Class', 'E-Class', 'S-Class', 'Maybach', 'V-Class', 'GLC', 'GLS', 'Maybach', 'G-Class', 'AMG-A-35', 'AMG-A-45', 'AMG-GLA-35', 'AMG-GLC-43', 'AMG-GLE-53', 'AMG-GLE-63', 'Ameo', 'Apollo', 'Atlantic', 'Beetle', 'Cabrio', 'Cabriolet', 'Carat', 'Caribe', 'ClÃ¡sico', 'Corrado', 'Corsar', 'Dasher', 'Derby', 'Golf', 'Iltis', 'K70', 'Logus', 'Lupo', 'Parati', 'Passat', 'Phaeton', 'Pointer', 'Polo', 'Vento', 'XL1'];
const brands = ['Abarth', 'Audi', 'Bentley', 'BMW', 'Buick', 'Cadillac', 'Chevrolet', 'Chrysler', 'Citroen', 'Dacia', 'Dodge', 'Ferrari', 'Fiat', 'Ford', 'GMC', 'Honda', 'Hummer', 'Hyundai', 'Infiniti', 'Isuzu', 'Jaguar', 'Jeep', 'Kia', 'Lamborghini', 'Lexus', 'Lincoln', 'Lotus', 'Maserati', 'Mazda', 'Mitsubishi', 'Nissan', 'Opel', 'Peugeot', 'Pontiac', 'Porsche', 'Renault', 'Skoda', 'Smart', 'Subaru', 'Suzuki', 'Tesla', 'Toyota', 'Volvo'];

const randomColor = (): string => {
  const number = Math.round(0xffffff * Math.random()).toString(16);
  const zero = '000000'.substring(0, (6 - number.length));
  return `#${zero}${number}`;
};
const randomName = (): string => {
  const value1 = brands[getRandom(0, brands.length - 1)];
  const value2 = models[getRandom(0, models.length - 1)];
  return `${value1} ${value2}`;
};

const randomCarGenerate = (count: number): {
  color: string, name: string, car: number,
}[] => Array(count)
  .fill({})
  .map(() => ({
    color: randomColor(),
    name: randomName(),
    car: getRandom(1, 6),
  }));

const helpers = {
  randomCarGenerate,
};

export default helpers;
