import {
  createCar, deleteCar, getCars, updateCar,
} from '../services/carService';
import { renderCart } from '../view/garage';
import { CarType, CreateWinnerFn, IsWinner } from '../types';
import helpers, { getRandom } from '../helpers';
import { renderPage, renderTotalCount } from '../view/info';
import { startCar, startStopEngine } from '../services/engineService';
import { madeWinner } from './winners';
import { renderNotifyMessage } from '../view/notifyMessage';
import { deleteWinner } from '../services/winnerService';
import { makeMoveCar, makeStopCar } from '../view/car';

let totalCars: number = 0;
let selectCar: CarType | null | undefined = null;
let carsInGarage: CarType[] = [];
const limitGarage: number = 7;
const page: { garage: number, winners: number } = { garage: 1, winners: 1 };
const animations: { [key: string]: Animation } = {};
const winner: IsWinner = {
  IsWinner: false,
  setIsWinner(params: boolean) {
    this.IsWinner = params;
  },
};

export const renderGarageInformation = async () => {
  const { totalCount, cars } = await getCars([{ key: 'limit', value: limitGarage }, { key: 'page', value: page.garage }]);
  carsInGarage = cars;
  totalCars = totalCount ? parseInt(totalCount, 10) : 0;
  renderTotalCount('garage', totalCars);
  renderPage('garage', page.garage);
  renderCart(cars);
  (document.querySelector('[data-race]') as HTMLButtonElement).disabled = false;
};
const createWinner = ({ id, name, winnerObj }: CreateWinnerFn) => (el: AnimationPlaybackEvent) => {
  if (!winnerObj.IsWinner) {
    winnerObj.setIsWinner(true);
    const secondTime = (el.currentTime ? el.currentTime / 1000 : 0).toFixed(2);
    renderNotifyMessage(`
     car ${name} win race is ${id} with time ${secondTime}
     `, 'success');
    madeWinner({ id, time: parseFloat(secondTime) });
  }
};

const startCarMoving = async (carBox: Element) => {
  const id = parseInt(carBox.id, 10);
  const { distance, velocity } = await startStopEngine(id, 'started');
  const time = distance / velocity;
  const name = carBox.querySelector('.car__name') as HTMLElement;
  const smog = carBox?.querySelector('.car__smog') as HTMLButtonElement;
  animations[id] = makeMoveCar(carBox, time, createWinner({
    id, name: name.innerHTML, winnerObj: winner,
  }));
  const drive = await startCar(id, 'drive');
  if (!drive && animations[id]) {
    smog.classList.add('_active');
    animations[id]?.pause();
  }
};

export const garageControlListener = async () => {
  window.addEventListener('click', async (e) => {
    const target = e.target as HTMLTextAreaElement;
    if (target.matches('[data-create]')) {
      e.preventDefault();
      const parent = target.closest('.controls__block');
      const name = (<HTMLInputElement>parent?.querySelector('.controls__input')).value;
      const color = (<HTMLInputElement>parent?.querySelector('.controls__color')).value;
      const carName = name || 'Delorean';
      await createCar({ name: carName, color, car: getRandom(1, 6) });
      renderGarageInformation();
    }
    if (target.matches('[data-update]')) {
      e.preventDefault();
      const parent = target.closest('.controls__block');
      const name = (<HTMLInputElement>parent?.querySelector('.controls__input')).value;
      const color = (<HTMLInputElement>parent?.querySelector('.controls__color')).value;
      const carId = (<HTMLInputElement>parent?.querySelector('#carId')).value;
      const updatedCar = await updateCar(parseInt(carId, 10), { name, color });
      carsInGarage = carsInGarage.map((car) => {
        if (car.id === updatedCar.id) {
          return updatedCar;
        }
        return car;
      });
      renderCart(carsInGarage);
    }
    if (target.matches('[data-race]')) {
      e.preventDefault();
      target.disabled = true;
      winner.setIsWinner(false);
      const cars = Array.from(document.querySelectorAll('.car'));
      cars.forEach((carBox) => startCarMoving(carBox));
    }
    if (target.matches('[data-reset]')) {
      e.preventDefault();
      const cars = Array.from(document.querySelectorAll('.car'));
      cars.forEach(async (carBox) => {
        await startStopEngine(parseInt(carBox.id, 10), 'stopped');
        makeStopCar(carBox);
        animations[carBox.id].cancel();
      });
      (document.querySelector('[data-race]') as HTMLButtonElement).disabled = false;
    }
    if (target.matches('[data-generate]')) {
      e.preventDefault();
      const cars = helpers.randomCarGenerate(100);
      const promiseArray = cars.map((car) => createCar(car));
      const result = await Promise.allSettled(promiseArray);
      result.filter((r) => r.status === 'fulfilled');
      renderGarageInformation();
    }

    if (target.matches('[data-select]')) {
      e.preventDefault();
      const car = target.closest('.car');
      if (car) {
        selectCar = carsInGarage.find((c) => c.id === parseInt(car.id, 10));
        const parent = document.querySelector('[data-controls="update"]');
        const text = (<HTMLInputElement>parent?.querySelector('.controls__input'));
        const color = (<HTMLInputElement>parent?.querySelector('.controls__color'));
        const carId = (<HTMLInputElement>parent?.querySelector('#carId'));
        text.value = selectCar ? selectCar?.name : '';
        color.value = selectCar ? selectCar?.color : '';
        carId.value = selectCar && selectCar.id ? selectCar?.id.toString() : '';
      }
    }
    if (target.matches('[data-remove]')) {
      e.preventDefault();
      const car = target.closest('.car');
      if (car) {
        deleteCar(parseInt(car.id, 10));
        deleteWinner(parseInt(car.id, 10));
        renderGarageInformation();
      }
    }
    if (target.matches('[data-start]')) {
      e.preventDefault();
      const carBox = target.closest('.car') as HTMLElement;
      startCarMoving(carBox);
    }
    if (target.matches('[data-stop]')) {
      e.preventDefault();
      const carBox = target.closest('.car') as HTMLElement;
      await startStopEngine(parseInt(carBox.id, 10), 'stopped');
      makeStopCar(carBox);
      animations[carBox.id].cancel();
    }

    if (target.matches('[data-prev]')) {
      e.preventDefault();
      const key = target.getAttribute('data-prev') as keyof typeof page;
      page[key] = page[key] <= 1 ? 1 : page[key] - 1;
      renderGarageInformation();
    }
    if (target.matches('[data-next]')) {
      e.preventDefault();
      const key = target.getAttribute('data-next') as keyof typeof page;
      const total = totalCars < limitGarage ? 1 : Math.ceil(totalCars / limitGarage);
      page[key] = page[key] < total ? page[key] + 1 : page[key];
      renderGarageInformation();
    }
  });
};
