import {
  FullWinner, SortType, Winner, WinnersDB,
} from '../types';
import { renderPage, renderTotalCount } from '../view/info';
import {
  getWinners, getWinner, createWinner, updateWinner,
} from '../services/winnerService';
import { renderWinners } from '../view/winners';
import { getCar } from '../services/carService';

const WINNERS_ON_PAGE: number = 10;
let totalWinners: number = 0;
let allWinners: WinnersDB = [];
let winnersSort: SortType = 'id';
let winnersOrder: boolean = true;
const page: { winners: number } = { winners: 1 };

export const winnersControl = async () => {
  const { totalCount, winners } = await getWinners([
    { key: 'sort', value: winnersSort },
    { key: 'order', value: winnersOrder ? 'ASC' : 'DESC' },
    { key: 'limit', value: WINNERS_ON_PAGE },
    { key: 'page', value: page.winners }]);
  const winnersFull = winners.map(async (car: Winner) => {
    const carInfo = await getCar(car.id);
    return { ...car, ...carInfo };
  });
  allWinners = await Promise.all(winnersFull);
  totalWinners = totalCount ? parseInt(totalCount, 10) : 0;
  renderTotalCount('winners', totalWinners);
  renderPage('winners', page.winners);
  renderWinners(allWinners as FullWinner[], page.winners);
};

export const madeWinner = async ({ id, time }: { id: number, time: number }) => {
  const winnerByDb = await getWinner(id);
  if (winnerByDb) {
    const newTime = time < winnerByDb.time ? time : winnerByDb.time;
    updateWinner(id, { wins: winnerByDb.wins + 1, time: newTime });
  } else {
    createWinner({ id, wins: 1, time });
  }
};

export const garageWinnersListener = async () => {
  window.addEventListener('click', async (e) => {
    const target = e.target as HTMLTextAreaElement;
    if (target.matches('[data-sort]')) {
      const dataValue = target.getAttribute('data-sort') as SortType;
      if (dataValue === winnersSort) {
        winnersOrder = !winnersOrder;
      }
      winnersSort = target.getAttribute('data-sort') as SortType;
      winnersControl();
    }
    if (target.matches('[data-prev]')) {
      e.preventDefault();
      const key = target.getAttribute('data-prev') as keyof typeof page;
      page[key] = page[key] <= 1 ? 1 : page[key] - 1;
      winnersControl();
    }
    if (target.matches('[data-next]')) {
      e.preventDefault();
      const key = target.getAttribute('data-next') as keyof typeof page;
      const total = totalWinners < WINNERS_ON_PAGE ? 1 : Math.ceil(totalWinners / WINNERS_ON_PAGE);
      page[key] = page[key] < total ? page[key] + 1 : page[key];
      winnersControl();
    }
  });
};
