import { CarType, Path, QueryParams } from '../types';

const baseUrl = 'http://localhost:3000';

const getQueryParams = (params: QueryParams): string => {
  if (params.length) {
    return `?${params.map((p) => `_${p.key}=${p.value}`).join('&')}`;
  }
  return '';
};

export const getCars = async (queryParams: QueryParams) => {
  const response = await fetch(`${baseUrl}${Path.garage}${getQueryParams(queryParams)}`);
  const cars = await response.json();
  const totalCount = response.headers.get('X-Total-Count');
  return { cars, totalCount };
};

export const getCar = async (id: number) => {
  const response = await fetch(`${baseUrl}${Path.garage}/${id}`);
  const car = await response.json();
  return car;
};

export const createCar = async (body: CarType) => {
  const response = await fetch(`${baseUrl}${Path.garage}`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(body),
  });
  const car = await response.json();
  return car;
};

export const updateCar = async (id: number, body: { color: string, name: string }) => {
  const response = await fetch(`${baseUrl}${Path.garage}/${id}`, {
    method: 'PUT',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(body),
  });
  const car = await response.json();
  return car;
};

export const deleteCar = async (id: number) => {
  const response = await fetch(`${baseUrl}${Path.garage}/${id}`, {
    method: 'DELETE',
  });
  const car = await response.json();
  return car;
};
