export enum Path {
  garage = '/garage',
  engine = '/engine',
  winners = '/winners',
}

export type SortType = 'id' | 'wins' | 'time';

export type CarType = {
  color: string;
  name: string;
  id?: number;
  car?: number;
};
export type CarDB = CarType[];

export type Winner = {
  id: number;
  wins: number;
  time: number;
};
export type IsWinner = {
  IsWinner: boolean,
  setIsWinner(params: boolean): void
};
export type CreateWinnerFn = { id: number, name: string, winnerObj: IsWinner };

export type FullWinner = Winner & CarType;
export type WinnersDB = Winner[];

export type QueryParams = { key: string, value: number | string }[];
