export default (page: 'garage' | 'winners') => `
<div class="info">
  <div class="info__elements">
    garage (<span data-count=${page} class="info__elements-count"></span>)
  </div>
  <div class="info__page">
    page #<span data-page=${page} class="info__page-count">1</span>
  </div>
</div>
`;

export const renderPage = async (data: string, page: number) => {
  const pageItem = document.querySelector(`[data-page=${data}]`);
  if (pageItem) {
    pageItem.innerHTML = page.toString();
  }
};

export const renderTotalCount = async (data: string, count: number) => {
  const countItem = document.querySelector(`[data-count=${data}]`);
  if (countItem) {
    countItem.innerHTML = count.toString();
  }
};
